/***********/
/*共通箇所*/
/***********/
body{
  width:95%;
  margin:0 auto;
  padding: 40px 0;
  background:#333;
  color:#FFF;
  font-size:14px;
}

/***********/
/*見出し*/
/***********/
.headLineUnderLineLarge{
  border-bottom:1px solid #FFF;
  padding:0 10px 10px;
  letter-spacing: 1.5px;
  font-size: 1.6em;

}
.headLineUnderLineSmall{
  border-bottom:1px solid #AAA;
  padding:0 5px 5px;
  letter-spacing: 1px;
  font-size: 1.4em;
  margin:40px 0 20px;
}

.headLineBar{
  padding:10px 15px;
  background:#333;
  color:#FFF;
  letter-spacing: 1px;
  font-size: 1.3em;
  margin: 40px 0 20px;
}

.headLineText{
  font-size:1em;
  font-weight:bold;
  color:#333;
  margin:20px 0 0;
}
.headLineText span{
  font-size:0.8em;
}

/***********/
/*テキスト系*/
/***********/
.caption{
  font-size:1em;
}
.error{
  color:#E00;
  font-weight:bold;
  font-size:1.2em;
}


/***********/
/*ボタン*/
/***********/
.buttonExport{
  padding:10px 20px;
  background:#FFF;
  border:none;
  border-radius:120px;
  font-size:1.2em;
  cursor: pointer;
}
.buttonExport:hover{
  opacity:0.8;
}
/***********/
/*エリア毎*/
/***********/
.status{
  width:95%;
  margin:40px auto;
  background:#FFF;
  padding:20px;
  color:#444;
}

.status p{
  margin: 20px 0;
  font-weight:bold;
}

.status p span{
  font-size:0.8em;
}

.textDelete{
  color:#E00;
}

.textMove{
  color:#070;
}

.textAdd{
  color:#00A;
}

.status__sql{
  border:1px double #DDD;
  padding:20px;
  background:#EEE;
  font-size:1.1em;
}
.status__sql p{
  margin:0 0 10px;
  font-weight:bold;
}
.status__sql p:last-of-type{
  margin:0;
}
.status__sql p span{
  font-size:0.8em;
  font-weight:normal;
}

.replaceList{
  margin:0;
}

.replaceList li{
  margin:10px 0 0;
  font-size:0.9em;
  line-height:1.6;
  list-style-type:circle;
}
.replaceList li span{
  font-weight:bold;
  padding:0 5px;
}

.loader,
.loader:before,
.loader:after {
  background: #ffffff;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
/***********/
/*ローダー*/
/***********/
.loader {
  color: #ffffff;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

